  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
  color: #333;
  letter-spacing: 0;
  font-family: PingFang SC, "微软雅黑" !important; }

html {
  line-height: 1; }

ol,
ul {
  list-style: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

caption,
td,
th {
  text-align: left;
  font-weight: 400;
  vertical-align: middle; }

blockquote,
q {
  quotes: none; }

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none; }

a img {
  border: none; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0; }

input[type="number"] {
  -moz-appearance: textfield; }

body {
  overflow-y: scroll;
  -webkit-text-size-adjust: 100% !important;
  text-size-adjust: 100% !important;
  -moz-text-size-adjust: 100% !important; }

.rol {
  width: 100%;
  float: left;
  box-sizing: border-box; }

#app {
  overflow-x: hidden;
  float: left;
  box-sizing: border-box; }
  @media (min-width: 1200px) {
    #app {
      width: 100%; } }
  @media (max-width: 1199px) {
    #app {
      width: 750px; } }

.m-box {
  width: 1100px;
  margin: 0 auto;
  box-sizing: border-box; }
  @media (min-width: 1200px) {
    .m-box {
      width: 1100px; } }
  @media (max-width: 1199px) {
    .m-box {
      width: 700px; } }

.lt {
  float: left;
  box-sizing: border-box; }

.gt {
  float: right;
  box-sizing: border-box; }

@font-face {
  font-family: 'iconfont';
  /* project id 773130 */
  src: url("//at.alicdn.com/t/font_773130_uo0ypcf2m6s.eot");
  src: url("//at.alicdn.com/t/font_773130_uo0ypcf2m6s.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_773130_uo0ypcf2m6s.woff") format("woff"), url("//at.alicdn.com/t/font_773130_uo0ypcf2m6s.ttf") format("truetype"), url("//at.alicdn.com/t/font_773130_uo0ypcf2m6s.svg#iconfont") format("svg"); }

.fa {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale; }

.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #2d49c6 !important; }

@media screen and (max-width: 1200px) {
  body {
    overflow-x: hidden; } }



