$breakpoints: (pc:1200px);
@mixin fontsize($size) {
    @include media('>=pc') {
        font-size: $size;
    }

    @include media('<pc') {
        font-size: $size * 0.8;
    }
    font-size: $size * 0.8;
}

@mixin property($property, $arg1, $arg2) {
    @include media('>=pc') {
        #{$property}: $arg1;
    }

    @include media('<pc') {
        #{$property}: $arg2;
    }
}


