@import "pie-mixin";


a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline;
    text-decoration:none;
    color:#333;
    letter-spacing: 0;
    font-family: PingFang SC ,'\5FAE\8F6F\96C5\9ED1'!important;

}

html {
    line-height: 1
}

ol,
ul {
    list-style: none
}

table {
    border-collapse: collapse;
    border-spacing: 0
}

caption,
td,
th {
    text-align: left;
    font-weight: 400;
    vertical-align: middle
}

blockquote,
q {
    quotes: none
}

blockquote:after,
blockquote:before,
q:after,
q:before {
    content: "";
    content: none
}

a img {
    border: none
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
    display: block
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button{
    -webkit-appearance: none !important;
    margin: 0;
}
input[type="number"]{-moz-appearance:textfield;}
body{
  overflow-y:scroll;
  -webkit-text-size-adjust: 100% !important;
  text-size-adjust: 100% !important;
  -moz-text-size-adjust: 100% !important;
}
.rol{
  width:100%;
  float:left;
  box-sizing: border-box;
}
#app{
  @include property(width, 100%,750px);
  overflow-x:hidden;
  float:left;
  box-sizing: border-box;
}
.m-box{
  width:1100px;
  @include property(width, 1100px,700px);
  margin:0 auto;
  box-sizing: border-box;
}
.lt{
  float:left;
  box-sizing: border-box;
}
.gt{
  float:right;
  box-sizing: border-box;
}
@font-face {
  font-family: 'iconfont';  /* project id 773130 */
  src: url('//at.alicdn.com/t/font_773130_uo0ypcf2m6s.eot');
  src: url('//at.alicdn.com/t/font_773130_uo0ypcf2m6s.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_773130_uo0ypcf2m6s.woff') format('woff'),
  url('//at.alicdn.com/t/font_773130_uo0ypcf2m6s.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_773130_uo0ypcf2m6s.svg#iconfont') format('svg');
}
